/* ==========================================================================
   Fonts
   ========================================================================== */

/* lato-300 - latin_latin-ext */
@font-face {
  font-family: 'Lato';
  font-style: normal;
  font-weight: 300;
  src: url('../fonts/lato-v11-latin_latin-ext-300.eot'); /* IE9 Compat Modes */
  src: local('Lato Light'), local('Lato-Light'),
       url('../fonts/lato-v11-latin_latin-ext-300.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
       url('../fonts/lato-v11-latin_latin-ext-300.woff2') format('woff2'), /* Super Modern Browsers */
       url('../fonts/lato-v11-latin_latin-ext-300.woff') format('woff'), /* Modern Browsers */
       url('../fonts/lato-v11-latin_latin-ext-300.ttf') format('truetype'), /* Safari, Android, iOS */
       url('../fonts/lato-v11-latin_latin-ext-300.svg#Lato') format('svg'); /* Legacy iOS */
}
/* lato-300italic - latin_latin-ext */
@font-face {
  font-family: 'Lato';
  font-style: italic;
  font-weight: 300;
  src: url('../fonts/lato-v11-latin_latin-ext-300italic.eot'); /* IE9 Compat Modes */
  src: local('Lato Light Italic'), local('Lato-LightItalic'),
       url('../fonts/lato-v11-latin_latin-ext-300italic.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
       url('../fonts/lato-v11-latin_latin-ext-300italic.woff2') format('woff2'), /* Super Modern Browsers */
       url('../fonts/lato-v11-latin_latin-ext-300italic.woff') format('woff'), /* Modern Browsers */
       url('../fonts/lato-v11-latin_latin-ext-300italic.ttf') format('truetype'), /* Safari, Android, iOS */
       url('../fonts/lato-v11-latin_latin-ext-300italic.svg#Lato') format('svg'); /* Legacy iOS */
}
